.promo {
  position: relative;
  display: flex;
  margin-top: -118px;
  padding-top: 258px;
  min-height: 878px;

  &::before {
    position: absolute;
    display: block;
    content: "";
    top: 0;
    width: 100%;
    height: 100%;
    background: url("../img/promo-image-bg-desktop@1x.png") top center no-repeat $promo-bg-color;
    background-size: cover;

    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      background: url("../img/promo-image-bg-desktop@2x.png") cover;
    }
  }

  @media (max-width: $tablet-width) {
    padding-top: 135px;
    min-height: 707px;
  }

  @media (max-width: $mobile-width) {
    padding-top: 138px;
    min-height: 693px;
  }
}

.promo__wrapper {
  position: relative;
  display: flex;
  padding-left: 71px;
  margin: 0 auto;
  width: 1563px;
  background-image: url("../img/promo-image@1x.png");
  background-position: top right;
  background-size: 705px 515px;
  background-repeat: no-repeat;

  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    background: url("../img/promo-image@2x.png") cover;
  }

  @media (max-width: $tablet-width) {
    // background-position: 167% top;
    background-size: 465px 340px;
  }

  @media (max-width: $mobile-width) {
    padding: 0 15px;
    // background-position: 141% top;
    background-size: 301px 220px;
  }
}

.promo__info {
  display: flex;
  flex-direction: column;
  width: 51%;

  @media (max-width: $tablet-width) {
    padding-left: 17px;
    width: 64%;
  }

  @media (max-width: $mobile-width) {
    width: 100%;
    padding-left: 0;
  }
}

.promo h2 {
  position: relative;
  margin: 58px 0 5px;
  text-transform: uppercase;
  font-family: $font-secondary;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  color: $white;

  @media (max-width: $tablet-width) {
    margin: 76px 0 10px;
    font-size: 60px;
    line-height: 70px;
  }

  @media (max-width: $mobile-width) {
    margin: 194px 0 10px;
    font-weight: 700;
    font-size: 40px;
    line-height: 47px;
    letter-spacing: 0.07em;
  }
}

.promo p {
  position: relative;
  margin-bottom: 43px;
  padding-left: 98px;
  padding-right: 15px;
  font-family: $font-default;
  font-weight: 400;
  font-size: 22px;
  line-height: 34px;
  color: $white;

  &::before {
    position: absolute;
    content: "";
    left: 4px;
    top: 16px;
    width: 66px;
    height: 2px;
    background-color: $white;
  }

  @media (max-width: $tablet-width) {
    padding-left: 51px;
    font-size: 18px;
    line-height: 156.8%;
    margin-bottom: 54px;

    &::before {
      width: 33px;
      left: 3px;
    }
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 41px;
    font-size: 16px;
  }
}

.promo__button {
  margin-left: 98px;
  padding: 17px 44px;
  width: 407px;
  height: 55px;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.05em;
  color: $white;
  background-color: $accent;

  @media (max-width: $tablet-width) {
    margin-left: 51px;
    padding: 17px 30px;
    width: 379px;
  }

  @media (max-width: $mobile-width) {
    width: 287px;
    margin: 0 auto;
  }
}

.promo__button-mobile {
  display: none;

  @media (max-width: $mobile-width) {
    display: inline-block;
  }
}

.promo__button-desktop {
  @media (max-width: $mobile-width) {
    display: none;
  }
}

.promo img {
  position: absolute;
  top: 32%;
  right: 9.3%;
  width: 36.5%;
  height: auto;

  @media (max-width: $tablet-width) {
    width: auto;
    height: 340px;
    left: 66%;
    top: 15.5%;
  }
}
